/* Background-color variant */
.results-area,
.criteria-category,
.accordion-content {
    .accordion:not(.active) {
        .accordion-heading:not(:hover):not(:focus-visible) {
            background-color: $light-grey;
        }
    }

    .accordion-content {
        background-color: $light-grey;
    }
}

.results-area,
.criteria-category {
  .accordion-content > {
    .accordion:not(.active) {
      .accordion-heading:not(:hover):not(:focus-visible){
        background-color: $white;
      }
    }

    .accordion {
      .accordion-content {
        background-color: $white;
      }
    }
  }
}